
import { computed, defineComponent, provide, ref } from '@vue/runtime-core';
import { FormCreateAssessment } from '@/types';
import AddQuestions from './AddQuestions.vue';
import Options from './Options.vue';
import OrderQuestions from './OrderQuestions.vue';

export default defineComponent({
  name: 'AssesstmentsCreation',
  components: {
    AddQuestions,
    Options,
    OrderQuestions,
  },
  setup() {
    const input = ref<FormCreateAssessment>({
      title: '',
    });
    provide('input', input);

    const form = ref();
    const tabs = {
      AddQuestions: '1. Add Questions',
      OrderQuestions: '2. Order Questions',
      Options: '3. Assessment Options',
    };
    const order = Object.keys(tabs);
    const tab = ref('AddQuestions');
    const current = computed(() => order.indexOf(tab.value));
    const next = computed(() => order[current.value + 1] || null);
    const submit = (e: Event) => {
      const target = e.target as HTMLFormElement;

      if (target.checkValidity()) {
        if (next.value) {
          tab.value = next.value;
        } else {
          //submit data
        }
      }
    };

    return { current, form, input, next, submit, tab, tabs };
  },
});
