<template>
  <article class="tab-content main">
    <div class="assesment-breadcrumbs">
      <div class="assesment-breadcrumbs__row">
        <!-- Left -->
        <div class="assesment-breadcrumbs__left">
          <ul class="assesment-breadcrumbs__list">
            <li v-for="(title, name) in tabs" :key="name" :class="{active: tab === name}">
              <a href="#" @click.prevent="tab = name"><span>{{ title }}</span></a>
            </li>
          </ul>
        </div>

        <!-- Right -->
        <div class="assesment-breadcrumbs__right">
          <div class="assesment-breadcrumbs__nav">
            <button type="submit" form="create" class="btn btn-next">
              {{ next ? 'Next Step' : 'Submit' }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <form id="create" :ref="form" class="content padded" @submit.prevent="submit">
      <component :is="tab" />
    </form>
  </article>
</template>

<script lang="ts">
import { computed, defineComponent, provide, ref } from '@vue/runtime-core';
import { FormCreateAssessment } from '@/types';
import AddQuestions from './AddQuestions.vue';
import Options from './Options.vue';
import OrderQuestions from './OrderQuestions.vue';

export default defineComponent({
  name: 'AssesstmentsCreation',
  components: {
    AddQuestions,
    Options,
    OrderQuestions,
  },
  setup() {
    const input = ref<FormCreateAssessment>({
      title: '',
    });
    provide('input', input);

    const form = ref();
    const tabs = {
      AddQuestions: '1. Add Questions',
      OrderQuestions: '2. Order Questions',
      Options: '3. Assessment Options',
    };
    const order = Object.keys(tabs);
    const tab = ref('AddQuestions');
    const current = computed(() => order.indexOf(tab.value));
    const next = computed(() => order[current.value + 1] || null);
    const submit = (e: Event) => {
      const target = e.target as HTMLFormElement;

      if (target.checkValidity()) {
        if (next.value) {
          tab.value = next.value;
        } else {
          //submit data
        }
      }
    };

    return { current, form, input, next, submit, tab, tabs };
  },
});
</script>

