<template>
  <div class="filter-item" :class="{active}">
    <div class="filter-item__header" @click="active = !active">
      <div class="filter-item__label">
        <slot name="title">
          {{ title }}
        </slot>
      </div>
      <div class="filter-item__icon">
        <svg viewBox="-8 -8 16 16">
          <path d="M-3 -1.5h6l-3 3l-3 -3z" stroke-width="1.5" stroke="#333" fill="#333" stroke-linejoin="round" />
        </svg>
      </div>
    </div>
    <div class="filter-item__body">
      <slot />
    </div>
  </div>
</template>

<script>
import { ref, watch } from 'vue';

export default {
  props: {
    title: String,
    modelOpen: Boolean,
    open: Boolean,
  },
  emits: ['update:modelOpen'],
  setup(props, ctx) {
    const active = ref(props.open || props.modelOpen);
    watch(props.modelOpen, (val, old) => {
      if (val !== old) active.value = val;
    });
    watch(active, (val, old) => {
      if (val !== old) ctx.emit('update:modelOpen', val);
    });
    return { active };
  },
};
</script>

<style lang="scss" scoped>
svg {
  margin-top: 5px;
  width: 1em;
  > path {
    transition: all .3s;
    .active & {
      transform: scale(1, -1);
    }
  }
}
</style>
